@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap");
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.css);
@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.6.0/dist/css/bootstrap.min.css);

body {
    background-color: #e0e3e8;
    font-family: "Rubik", sans-serif;
}

a, a:hover, a:focus {
    text-decoration: none;
    transition: 0.5s;
    outline: none;
    color: inherit;
    display: block;
}

.btn-area{
  display: flex;
  gap: 5px;
}

button{
  outline: none;
  border: none;
  background-color: #ebedf0;
  color: #475F7B;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.3px;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  background-color: #fff;

  &:hover {
    background-color: #5a8dee;
    color: #fff;
  }
}

label{
  font-weight: 500;
  font-size: 14px;
  position: relative;
  margin-bottom: 0;
  color: #475F7B;
  transition: 0.3s;
  line-height: 1.2;
  margin-top: 0;
}

input, textarea {
  padding: 15px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  outline: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: #727E8C;
}

.nav-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0 1.5rem;

  h2{
    color : #5a8dee;
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.2;
    margin: 0 0 0.5rem;
  }
}

.container {
  margin: 3rem 0;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  > div {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }

  .content {
    margin-top: 0.5rem;
    flex: 0 0 65%;
    max-width: 65%;

    .card {
      box-shadow: 0px 5px 10px rgba(90, 116, 148, 0.3);
      font-size: 13px;
      font-weight: 500;
      letter-spacing: 0.3px;
      color: #475f7b;
      overflow: hidden;

      position: relative;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
      flex-direction: column;
      min-width: 0;
      word-wrap: break-word;
      background-color: #fff;
      background-clip: border-box;
      border: 1px solid rgba(0,0,0,.125);
      border-radius: 0.25rem;

      .card-body{
        flex: 1 1 auto;
        min-height: 1px;
        padding: 1.25rem;
      }
    }
  }
}

.accordion-card{
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 10px;
  overflow: hidden;

  .toggle{
    display: flex;
    align-items: flex-start;
    cursor: pointer;
    justify-content: space-between;
    background-color: #ebedf0;
    transition: 0.3s;
    padding: 1rem;

    &.active {
      background-color: #5a8dee;

      .title{
        color: #fff;
      }

      .icon {
        transform: rotate(180deg);
        color: #fff;
      }
    }

    .title{
      font-weight: 500;
      font-size: 14px;
      position: relative;
      margin-bottom: 0;
      color: #475F7B;
      transition: 0.3s;
      line-height: 1.2;
      margin-top: 0;
    }

    .icon {
      position: relative;
      top: 2px;
      color: #475F7B;
      transition: 0.35s;
      font-size: 12px;
    }
  }

  .con {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
    width: 100%;

    &:show{
        height: auto;
    }

    > div {
      flex: 1 1 auto;
      min-height: 1px;
      padding: 15px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      p{
        margin: 0;
        width: 100%;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        color: #727E8C;
      }

      button {
        &:hover{
          background-color: lightcoral;
        }
      }
    }
  }
}

.add-content {
  form {
    display: flex;
    flex-direction: column;
    align-items: center;

    > div {
      width: 100%;
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;
    } 
  }
}